@import url(https://fonts.googleapis.com/css?family=Barlow&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cutive+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#navigationBar {
    display: inline-block;
    width: 100%;
    margin-top: 20px;
    margin-bottom: 40px;
}

#name {
    font-family: 'Barlow', sans-serif;
}

#intro {
    font-size: 15px;
}


#nav-container {
    display: flex;
    justify-content: space-between;
    margin: auto;
}

#navList {
    display: flex;
    min-width: 100%;
    list-style: none;
    justify-content: space-evenly;
    padding: 0px;    
}

#navList a {
    text-decoration: none;
    color: yellow;
    margin: 0px;
    font-size: 16px;
}
.home-container {
    margin-top: 10px;
    margin-bottom: 20px;
}

.img-container {
    max-width: 200px;
    height: auto;
}

img {
    border-radius: 50%;
}

.welcome {
    margin-top: 40px;
    font-family: 'Cutive Mono', monospace;    
    font-size: 17px;
}
#about {
    text-align: left;
}

.heading {
    font-size: 25px;
    color: yellow;
}


#tools {
    display: block;
    list-style-type: square;
}
#projects-container {
    margin-top: 30px;
    margin-bottom: 20px;
}

#projects img {
    border-radius: 0%;
}
#contact {
    padding: 20px;
    min-height: 30vh;
}

#footer * {
    padding: 0;
    margin: 0;
}

#footer {
    position: relative;
    bottom: 5px;
    margin: auto;
    margin-top: 10px;
    
}


#socialIcons {
    width: 200px;
    display: inline-block;
    margin-bottom: 10px;
}

#socialIcons ul {
    display: inline-flex;
    min-width: 100%;
    justify-content: space-between;
}


#footer ul li {
    list-style: none;
    height: 40px;
    width: 40px;
    font-size: 1.3rem;
    border: 2px solid yellow;
    border-radius: 100%;
}

#footer a {
    text-decoration: none;
    color: white;
}

#copy {
    margin-top: 20px;
    font-size: 15px;
}

* {
    font-family: 'Quicksand', sans-serif;
    background-color: #19011A;
    color: rgb(197, 182, 182);
    font-size: 18px;
}

.container {
    display: grid;
    grid-template-rows: auto auto auto;
}
